const ProfilePage = (props: IndexPageProps) => {
  const { data } = props;
  const title = data.site.siteMetadata.title;

  return (
    <Layout>
    <SEO title={title} />
      <div className="profile-wrap">
      <h3 style={{marginBottom: "0.9rem", marginTop: "0.0rem"}}>About this site</h3>
      <p>This site is written mostly using Gatbsy and Prism. Here I make public notes about computer science.
      </p>
      <h3 style={{marginBottom: "0.9rem" }}>Find me online</h3>
      <ul style={{marginTop: '0', marginBottom:'17px', marginLeft:'0px', listStyleType: 'none'}}>
        <li>
          <a target="_blank" className="link-style" href="https://github.com/Nmroth42" > Github</a>
        </li>
        <li>
          <a target="_blank" className="link-style"  href="https://t.me/Nmroth42" > Telegram </a>
        </li>
        <li>
          <a target="_blank" className="link-style"  href="https://goodreads.com/nmroth42" >  Goodreads  </a>
        </li>
      </ul>
      </div>


    </Layout>
  )
};


export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`;

export default ProfilePage;
